
.smart-grid.selectable:not(.multiple) td{cursor:pointer
}
.smart-grid.selectable:not(.multiple) tr.checked td{background-color:#eff7ff
}
.smart-grid.hoverable tbody tr:hover td{transition:all .3s;background-color:#f5f4f1
}
.smart-grid.inline tbody tr{float:left;padding:10px
}
.smart-grid.inline tbody tr td{display:block
}
.smart-grid.none td,.smart-grid.none th{border:none !important
}
.smart-grid.x td,.smart-grid.x th{border-left:none !important;border-right:none !important
}
.smart-grid.y td,.smart-grid.y th{border-top:none !important;border-bottom:none !important
}
.smart-grid table{border-spacing:0;width:100%
}
.smart-grid thead+tbody tr:first-child td{border-top:0
}
.smart-grid th,.smart-grid td{padding:8px 12px;text-align:left
}
.smart-grid th.checkbox-cell,.smart-grid td.checkbox-cell{width:30px;text-align:center
}
.smart-grid th:not(:first-child),.smart-grid td:not(:first-child){border-left:0
}
.smart-grid th{background-color:#fbfaf7;border:1px solid #e4e4dc;color:#333;font-size:14px
}
.smart-grid th .sort-place{display:none
}
.smart-grid th.sort{cursor:pointer;transition:all .3s
}
.smart-grid th.sort.asc,.smart-grid th.sort.desc{background-color:#e7f6fd
}
.smart-grid th.sort:not(.img-sort).asc .sort-place{background:url(../assets/images/asc.png) no-repeat center center
}
.smart-grid th.sort:not(.img-sort).desc .sort-place{background:url(../assets/images/desc.png) no-repeat center center
}
.smart-grid th.sort:not(.img-sort) .sort-place{display:inline-block;position:relative;top:2px;width:14px;height:14px;background:url(../assets/images/sort.png) no-repeat center center;transition:all .3s
}
.smart-grid tr:last-child td{border-bottom:1px solid #f2f1ec
}
.smart-grid td{color:#666;font-size:12px;border:1px solid #f2f1ec;border-bottom:0
}
.smart-grid td>.empty-cell{display:inline-block;height:14px;width:14px
}
.smart-grid td.show-more{text-align:center;cursor:pointer
}
.smart-grid .layer{position:absolute;top:0;left:0;bottom:0;right:0;background:url(../assets/images/loading.gif) no-repeat center center rgba(192,192,192,0.5);z-index:-1;opacity:0;text-align:center
}
.smart-grid.loading{position:relative
}
.smart-grid.loading .layer{transition:all .3s;z-index:1;opacity:1
}
.smart-grid .hidden{display:none;visibility:hidden
}
.smart-grid .grid-checkbox{display:inline-block;height:14px;width:14px;line-height:14px;border-radius:4px;border:1px solid #e4e4dc;overflow:hidden
}
.smart-grid .grid-checkbox .checkbox-wrap{display:inline-block;position:relative;background-color:#fff;height:100%;width:100%
}
.smart-grid .grid-checkbox .checkbox-wrap::before,.smart-grid .grid-checkbox .checkbox-wrap::after{content:" ";display:inline-block;opacity:0
}
.smart-grid .grid-checkbox .checkbox-wrap::before{position:absolute;width:5px;height:10px;top:-1px;right:2.5px;transform:rotate(45deg) scale(.7);border-right:2px solid #fff;border-bottom:2px solid #fff
}
.smart-grid .grid-checkbox .checkbox-wrap::after{background-color:#0c8fd3;height:100%;width:100%;cursor:pointer;border-radius:2px
}
.smart-grid .grid-checkbox .checkbox-wrap.checked::before,.smart-grid .grid-checkbox .checkbox-wrap.checked::after{transition:all .15s;opacity:1
}


